<template>
  <div class="film" v-if="film">
      {{film.data[0].title}}
      <iframe class="film__video" :src="film.data[0].iframe_src" frameborder="0" allowfullscreen></iframe>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

export default {
  name: 'Home',
  computed: {
    ...mapGetters(['film'])
  },
  methods: {
    ...mapActions(['getFilm'])
  },
  mounted() {
    this.getFilm(this.$route.params.id)
  },
}
</script>
<style>
    .film {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .film__video {
      width: 50vw;
      height: 28vw;
    }
</style>